    <div class ="bg-light"> <br>
        <form>
  <div class="form-group">
    <label for="exampleInputEmail1">First Name</label>
    <input type="text" class="form-control" id="first_name" v-model="form.first_name">
    <!-- <span v-if="!$v.name.required && $v.name.$dirty" class="text-danger">Name is required!</span> <br> -->
    </div>

  <div class="form-group">
    <label for="exampleInputEmail1">Last Name</label>
    <input type="text" class="form-control" id="last_name" v-model="form.last_name">
   

  </div>
  <div class="form-group">
    <label for="exampleInputEmail1">Email address</label>
    <input type="email" class="form-control" id="email" v-model="form.email" aria-describedby="emailHelp">
    <!-- <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small> -->
  </div>
  <div class="form-group">
    <label for="exampleInputPassword1" >Password</label>
