<style scoped>

</style>

<template>
 <div class="form-group">
        <form @submit.prevent="login">

        
    <div class="form-group">
    <label for="exampleInputEmail1">Email address</label>
    <input type="email" class="form-control" id="email" v-model="user.email" aria-describedby="emailHelp">
    <!-- <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small> -->
  </div>


  <div class="form-group">
    <label for="exampleInputPassword1" >Password</label>
    <input  type="password" class="form-control" id="password" v-model="user.password">
    <!-- <span v-if="!$v.password.required && $v.password.$dirty" class="text-danger">Password is required!</span> <br>
     <span v-if="!$v.password.minLength && $v.password.$dirty" class="text-danger">Password must contain at least 6 alphanumeric characters</span> -->
  </div>
  <button type="submit" class="btn btn-sm btn-primary btn-block"><img src="../assets/login.svg"/></button>
  <button v-if="userToken" class="btn btn-danger" @click="logout()">Logout</button>

</form>
</div>
<!-- Bootstrap form for login
  <div class ="container text-center py-5">
    <div class="card">
      <div class="card-header">
      <h4>PLEASE LOG IN BELOW</h4>
      </div>
      <br>
      <div class="card-body">
       <form @submit.prevent="onSubmit">
         <div class="form-group">
           <label for="exampleInputEmail1"><h5>EMAIL</h5></label>
           <div class="input-group mb-3">
             <div class="input-group-prepend">
               <span class="input-group-text" id="basic-addon1"><img src="../assets/email.svg"/></span>
             </div>
             <input type="text" class="form-control" placeholder="Enter email address" aria-label="Emaill" aria-describedby="basic-addon1">
            </div>
          </div>
         <div class="form-group">
           <label for="exampleInputPassword1"><h5>PASSWORD</h5></label>
           <div class="input-group mb-3">
             <div class="input-group-prepend">
               <span class="input-group-text" id="basic-addon2"><img src="../assets/password.svg"/></span>
             </div>
             <input type="text" class="form-control" placeholder="Enter password" aria-label="Password" aria-describedby="basic-addon2">
            </div>
         </div>
         <br>
         <button type="submit" class="btn btn-sm btn-primary btn-block"><img src="../assets/login.svg"/></button>
         <button v-if="userToken" class="btn btn-danger" @click="logout()">Logout</button>
         <br><p>Don't have an account? <router-link to="/register"><button type="button" class="btn btn-sm btn-outline-dark"><b>Sign Up</b></button></router-link></p>
         <p v-if="status" class= "danger font-italic font-weight-bold text-danger text-center">{{status}}</p>
       </form> 
       </div>
    </div>
  </div> -->
 
</template>


<script>

// import axios from 'axios'
import SlotService from "@/services/SlotService";


export default {
    name: 'Login',
     

    data(){
        return{       
            user: {
              email: null,
              password: null,
              token: null
            },
            status: null ,
            inputRules: [
              v => ( v && v.length >= 6) 
              ] 
        }
    },
   methods:{
     //Method to login 
     
          login(){
            console.log("This works")
                  SlotService.login({User: this.user})

                 .then(res=>{
                   if (res.data === 'Invalid password')
                   {
                     console.log("Invalid password was entered; please enter the correct password")
                    //  this.$router.push('/')
                   }
                   else {
                    // this.status = null
                    // this.user.acc_id=res.data.Acc_ID
                    // console.log("here is account data: " + res.data);
                    let dummyuser = res.data;
                    // console.log(dummyuser);
                    // let userLogin = {acc_id: dummyuser[0].Acc_ID, email: dummyuser[0].Email, password: dummyuser[0].Password}
                    // console.log("Here is user information: " + userLogin.acc_id)

                    let email = dummyuser[0].Email;
                    let password = dummyuser[0].Password;
                    let acc_id = dummyuser[0].Acc_ID

                    console.log ("account info: ")

                    this.$store.commit('loginUser', {email, password, acc_id})
                    // this.$alert("Successfully logged in");
                    console.log("Successful login");

                    // console.log("Here is acc_id token: " + this.$store.getters.getUser);

                    this.$router.push('Home')
                    // this.$router.go('/Home')
                   }
          
             
                 }).catch(err=>{ 
                  // this.errors = err.response.data.errors;
                  console.log(err)
                  // this.status = err
                  })
             },
          

    logout(){
      const token = null
      this.$store.commit('logout', {token})
      // this.$router.push('/')
    },

   },
  //Getters for token
   computed:{
      userToken(){
        return this.$store.getters.getUser
      }
    }
 
}
</script>


