<!-- Transactions Page -->
<template>
  <div class="Transactions py-3">
    <div v-if="transactions.length >= 0 && userToken" class="container">
      <nav class="navbar navbar-expand-lg">
        <ul class="nav nav-tabs ml-auto">
          <li class="nav-item">
            <router-link to="/inventory" class="nav-link text-dark"> <img src="../assets/inventory.svg" alt=""> Inventory </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/transactions" class="nav-link text-dark active"> <img src="../assets/transaction.svg" alt=""> Transactions</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/customers" class="nav-link text-dark"> <img src="../assets/transaction.svg" alt=""> Customers</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/suppliers" class="nav-link text-dark"> <img src="../assets/transaction.svg" alt=""> Suppliers</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/" class="nav-link text-dark"> <img src="../assets/logout.svg" alt=""> Log Out</router-link>
          </li>
        </ul>
      </nav>
      <div class="mb-4">
        <h1 class="display-3">TRANSACTIONS</h1>
      </div>
      <!-- Table holding data of Transactions
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>Order #</th>
            <th>Supplier</th>
            <th>Customer</th>
            <th>Chemical Compound Name</th>
            <th>Quantity Shipped</th>
            <th>Reserve</th>
            <th>Tracking</th>
            <th>Notes</th>
            <th>Action</th>
          </tr>
        </thead>
        Displays the data from the database
        <tbody>
          <tr v-for="transaction in transactions" :key="transaction.Trans_ID">
            <td>{{ transaction.Order_No }}</td>
            <td>{{ transaction.Supplier }}</td>
            <td>{{ transaction.Cust_Name }}</td>
            <td>{{ transaction.Compound }}</td>
            <td>{{ transaction.Quantity_Shipped }}</td>
            <td>{{ transaction.Reserve }}</td>
            <td>{{ transaction.Tracking_Link }}</td>
            <td>{{ transaction.Trans_Notes }}</td>
            <td>
              <router-link
                class="d-inline p-2 btn btn-outline-success btn-sm btn-block"
                v-bind:to="{ name: 'ViewTransaction', params: {selectedTransaction: transaction, id: transaction.Trans_ID } }"
                ><img src="../assets/read.svg"
              /></router-link>
              &nbsp;
              <router-link
                class="d-inline p-2 btn btn-outline-warning btn-sm btn-block"
                v-bind:to="{ name: 'UpdateTransaction', params: {selectedTransaction: transaction, id: transaction.Trans_ID } }"
                ><img src="../assets/update.svg"
              /></router-link>
              &nbsp;
              <a
                class="d-inline p-2 btn btn-outline-danger btn-sm btn-block"
                @click="deleteTransaction(transaction.Trans_ID)"
                ><img src="../assets/delete.svg"
              /></a>
            </td>
          </tr>
        </tbody>
      </table> -->

      <b-row>
        <b-col md="3">
          <b-form-input v-model="filter" type="search" placeholder="Search here"></b-form-input>
        </b-col>
      </b-row>
      <br>
      <b-row>
        <b-col>
          <b-table hover :items="transactions" :filter="filter" :fields="fields">
            <template v-slot:cell(Reserve)="data">
              <span v-if="data.item.Trans_Type ==='Sell'">
                {{data.item.Quantity_Produced - data.item.Quantity_Shipped}}
              </span>
              <span v-if="data.item.Trans_Type ==='Buy'">
                {{data.item.Quantity_Produced + data.item.Quantity_Shipped}}
              </span>
            </template>
            <template v-slot:cell(actions)="data">
              <router-link 
                  class="d-inline p-2 btn btn-outline-success btn-sm btn-block" 
                  v-bind:to="{ name: 'ViewTransaction', params: {selectedTransaction: data.item, id: data.item.Trans_ID } }">
                  <img src="../assets/read.svg"/>
              </router-link>
              &nbsp;
              <router-link 
                  class="d-inline p-2 btn btn-outline-warning btn-sm btn-block" 
                  v-bind:to="{ name: 'UpdateTransaction', params: {selectedTransaction: data.item, id: data.item.Trans_ID } }">
                  <img src="../assets/update.svg"/>
              </router-link>
              &nbsp;
              <a 
              class="d-inline p-2 btn btn-outline-danger btn-sm btn-block" 
              @click="deleteTransaction(data.item.Trans_ID)">
                  <img src="../assets/delete.svg"/>
              </a>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <br />
      <div class="row">
        <div class="col">
          <router-link
            class="btn btn-primary btn-sm btn-block"
            v-bind:to="{ name: 'AddTransaction' }">
            <b>ADD</b>
          </router-link>
        </div>
        <div class="col">
          <router-link
            class="btn btn-secondary btn-sm btn-block"
            v-bind:to="{ name: 'Home' }">
            <b>RETURN</b>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SlotService from "@/services/SlotService";
export default {
  name: "Transactions",
  data() {
    return {
      transactions: [],
      filter: "",
      fields: ['Order_No', 'Supplier', 'Compound', 'Quantity_Shipped', 'Reserve', 'Tracking_Link', 'actions']
    };
  },
  mounted() {
    this.getTransactions();
    // Tells that once page is loaded, this command will be executed
          const newUser = this.$store.getters.getUser
          this.user = newUser
          console.log(this.user)

  },
  computed: {
    userToken() {
      return this.$store.getters.getUser
    }
  },

  //Methods for CRUD operations
  methods: {
    async getTransactions() {
      var response = await SlotService.getTransactions();
      this.transactions = response.data;
      console.log(this.transactions);
    },

    async deleteTransaction(id) {
   
        await this.$confirm("Are you sure you want to delete this record?").then(response => {
               SlotService.deleteTransaction(id).then(response => {
                console.log(response.data);
                this.getTransactions();
                this.$alert("Record has been deleted")              
            }).catch(err=>{
            console.log(err)
            })
        }).catch(err => {
            console.log(err)
        })
    },

  },
};
</script>
<style>
</style>



