<template>
    <div class="container text-center">
        <div class="card mb-3">
          <div class="card-body">
            <h1 class="card-title display-3">
              WELCOME TO <b>AOS</b><br />INVENTORY MANAGEMENT SYSTEM
            </h1>
            <!-- Inventory button -->
            <div class="row">
              <div class="col">
                <router-link
                  class="btn btn-outline-dark btn-block"
                  v-bind:to="{ name: 'Inventory' }">
                  <img src="../assets/inventory.svg"/> INVENTORY
                </router-link>
              </div>
              
              <!-- Transactions button -->
              <div class="col">
                <router-link
                  class="btn btn-outline-dark btn-block"
                  v-bind:to="{ name: 'Transactions' }">
                  <img src="../assets/transaction.svg"/> TRANSACTIONS
                </router-link>
              </div>
              <div class="col">
                <router-link
                  class="btn btn-outline-dark btn-block"
                  v-bind:to="{ name: 'Customers' }">
                  <img src="../assets/transaction.svg"/> CUSTOMERS
                </router-link>
              </div>
              <div class="col">
                <router-link
                  class="btn btn-outline-dark btn-block"
                  v-bind:to="{ name: 'Suppliers' }">
                  <img src="../assets/transaction.svg"/> SUPPLIERS
                </router-link>
              </div>
            </div>
            <div class = "row">
                <label for="cas_no">Email</label>
              <input
              type="text"
              class="form-control"
              name="cas_no"
              placeholder="Enter your email"
              v-model="email"
              />
              </div>
            <div class = "row">
                <div class = "column">
                  <a 
              class="d-inline p-2 btn btn-sm btn-block" 
              @click="getTransactions(email)">
                  Transactions
              </a>
                </div>
                <div class = "column">
                  <a 
              class="d-inline p-2 btn btn-sm btn-block" 
              @click="getProducts(email)">
                  Products
              </a>
                </div>
                <div class = "column">
                  <a 
              class="d-inline p-2 btn btn-sm btn-block" 
              @click="getCustomers(email)">
                  Customers
              </a>
                </div>
              </div>
          </div>
        </div>
    </div>
</template>

<script>
import SlotService from "@/services/SlotService";
export default {
    name: "Home",
  data() {
    return {
      email: ""
      
    };
  },
  mounted() {
    // Tells that once page is loaded, this command will be executed
          // const newUser = this.$store.getters.getUser
          // this.user = newUser
          // console.log(this.user)
    if (this.userToken==true) {
      const newUser = this.$store.getters.getUser
      this.user = newUser
      console.log(this.user);
    }
  },

  computed: {
    userToken() {
      return this.$store.getters.getUser
    }
  },

  methods: {
    async getCustomers(email) {
      const response = await SlotService.getCustomerReports(email);
    },
    async getTransactions(email) {
      const response = await SlotService.getTransactionReports(email);
    },
    async getProducts(email) {
      const response = await SlotService.getProductReports(email);
    }
  }
}
</script>

<style scoped>

</style>


