import Vue from 'vue'
import Router from 'vue-router'
import Inventory from '@/components/Inventory'
import ViewInventory from '@/components/ViewInventory'
import AddInventory from '@/components/AddInventory'
import UpdateInventory from '@/components/UpdateInventory'
// import DeleteInventory from '@/components/DeleteInventory'
import Transactions from '@/components/Transactions'
import ViewTransaction from '@/components/ViewTransaction'
import AddTransaction from '@/components/AddTransaction'
import UpdateTransaction from '@/components/UpdateTransaction'
import Customers from '@/components/Customers'
import ViewCustomer from '@/components/ViewCustomer'
import AddCustomer from '@/components/AddCustomer'
import UpdateCustomer from '@/components/UpdateCustomer'
import Suppliers from '@/components/Suppliers'
import ViewSupplier from '@/components/ViewSupplier'
import AddSupplier from '@/components/AddSupplier'
import UpdateSupplier from '@/components/UpdateSupplier'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Register from '@/components/Register'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Login,
      name: 'Login'
    },
    {
      path: '/register',
      component: Register,
      name: 'Register'
    },
    {
      path: '/home',
      component: Home,
      name: 'Home'
    },
    {
      path: '/inventory',
      component: Inventory,
      name: 'Inventory'
    },
    {
      path:'/inventory/:id',
      component: ViewInventory,
      name: 'ViewInventory'
    },
    {
      path: '/inventory/add',
      component: AddInventory,
      name: 'AddInventory'
    },
    {
      path: '/inventory/:id/update',
      component: UpdateInventory,
      name: 'UpdateInventory'
    },
    // {
    //   path: '/inventory/:id/delete',
    //   component: DeleteInventory,
    //   name: 'DeleteInventory'
    // },
    {
      path: '/transactions',
      component: Transactions,
      name: 'Transactions'
    },
    {
      path: '/transactions/:id',
      component: ViewTransaction,
      name: 'ViewTransaction'
    },
    {
      path: '/transactions/add',
      component: AddTransaction,
      name: 'AddTransaction'
    },
    {
      path: '/transactions/:id/update',
      component: UpdateTransaction,
      name: 'UpdateTransaction'
    },
    // {
    //   path: 'transactions/:id/delete',
    //   component: DeleteTransaction,
    //   name: 'DeleteTransactions'
    // }
    {
      path: '/customers',
      component: Customers,
      name: 'Customers'
    },
    {
      path: '/customers/:id',
      component: ViewCustomer,
      name: 'ViewCustomer'
    },
    {
      path: '/addcustomer',
      component: AddCustomer,
      name:'AddCustomer'
    },
    {
      path: '/customers/:id/update',
      component: UpdateCustomer,
      name: 'UpdateCustomer'
    },
    {
      path: '/suppliers',
      component: Suppliers,
      name: 'Suppliers'
    },
    {
      path: '/suppliers/:id',
      component: ViewSupplier,
      name: 'ViewSupplier'
    },
    {
      path: '/addsupplier',
      component: AddSupplier,
      name: 'AddSupplier'
    },
    {
      path: '/suppliers/:id/update',
      component: UpdateSupplier,
      name: 'UpdateSupplier'
    },
  ]
})



